export const useUserStore = create(
	persist(
		(set, get) => ({
			// State
			user: null,
			isAuthenticated: false,
			processes: [],
			selectedProcess: null,
			isLoading: false,
			error: null,

			// Actions
			login: async (email, password) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 1000));

				const user = MOCK_USERS.find(
					(u) => u.email === email && u.password === password
				);

				if (user) {
					const { password: _, ...userWithoutPassword } = user;
					set({
						user: userWithoutPassword,
						isAuthenticated: true,
						isLoading: false,
					});
					return { success: true };
				} else {
					set({
						error: "Email ou senha incorretos",
						isLoading: false,
					});
					return { success: false, error: "Email ou senha incorretos" };
				}
			},

			logout: () => {
				set({
					user: null,
					isAuthenticated: false,
					processes: [],
					selectedProcess: null,
					error: null,
				});
			},

			fetchProcesses: async () => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 800));

				const userId = get().user?.id;
				if (!userId) {
					set({ error: "Usuário não autenticado", isLoading: false });
					return;
				}

				const userProcesses = MOCK_PROCESSES.filter((p) => p.userId === userId);

				set({
					processes: userProcesses.map((p) => ({
						id: p.id,
						processNumber: p.processNumber,
						title: p.title,
						description: p.description,
						status: p.status,
						lastUpdate: p.lastUpdate,
						createdAt: p.createdAt,
					})),
					isLoading: false,
				});
			},

			selectProcess: async (processId) => {
				set({ isLoading: true, error: null });

				// Simula delay de API
				await new Promise((resolve) => setTimeout(resolve, 600));

				const process = MOCK_PROCESSES.find((p) => p.id === processId);

				if (process) {
					set({ selectedProcess: process, isLoading: false });
				} else {
					set({ error: "Processo não encontrado", isLoading: false });
				}
			},

			clearSelectedProcess: () => {
				set({ selectedProcess: null });
			},

			exportProcessToPdf: async (processId) => {
				set({ isLoading: true });

				// Simula delay de geração do PDF
				await new Promise((resolve) => setTimeout(resolve, 1500));

				const process = MOCK_PROCESSES.find((p) => p.id === processId);

				if (process) {
					console.log("Gerando PDF para o processo:", process.processNumber);
					// Aqui você implementaria a lógica real de geração de PDF
					alert(
						`PDF do processo ${process.processNumber} seria gerado aqui. Funcionalidade será implementada com biblioteca de PDF.`
					);
				}

				set({ isLoading: false });
			},
		}),
		{
			name: "user-storage",
			partialize: (state) => ({
				user: state.user,
				isAuthenticated: state.isAuthenticated,
			}),
		}
	)
);

